body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.image-slider-container {
  position: relative;
  width: 100%;
  /* max-width: 800px; Set the max width of the slider container */
  margin: 0 auto; /* Center the slider */
  overflow: hidden;
}

.image-slider {
  display: flex;
  transition: transform 1s ease;
}

.slide {
  flex-shrink: 0;
  width: 100%;
}

.active {
  flex-shrink: 0;
  width: 100%;
}

.slider-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: transparent;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}

/* Tailwind code */

/* gallery */
@layer base {
  .gallery {
    @apply bg-color-100;
  }
  .gallery-title {
    @apply text-center text-5xl;
  }
}

/* works */
#works {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Display three items in a row */
  gap: 20px;
}

.container {
  width: 100%;
  height: auto;
}

.container-detail {
  padding: 10px;
  text-align: center;
}
@media (max-width: 768px) {
  #works {
    grid-template-columns: repeat(2, 1fr); /* Display two items in a row for smaller screens */
  }
}

@media (max-width: 480px) {
  #works {
    grid-template-columns: 1fr; /* Display one item in a row for even smaller screens */
  }
}






