@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap");

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Titillium Web", sans-serif;
  /* overflow-x:hidden; */
  
}

:root {
	--mainColor: #aa8b56;
	--mainColorLight: #4f6f52;
	--secondaryColor: #db2b39;
	--textColor: #eee;
}
.logo{
  padding: 0 10px;
  font-weight: bold;
  font-size: xx-large;
}
@media only screen and (max-width: 400px) {
  .logo{
    font-size: x-large;
  }
}

header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 80px;
	padding:0% 3%;
	background-color: var(--mainColor);
	color: var(--textColor);
}

nav a {
	margin: 0 2rem;
	color: var(--textColor);
	text-decoration: none;
  font-weight: bold;
  font-size: 1.2rem;
}

nav a:hover {
	color: var(--secondaryColor);
}

header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
	border: none;
	outline: none;
	color: var(--textColor);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;

}
.contact{
  padding: 0 10px;
  font-weight: bold;
  font-size: 1.2rem;
}
header div,
nav {
	display: flex;
	align-items: center;
}

@media only screen and (max-width: 512px) {
  .contact{
    display:none;
  }
  header{
    height:12vh;
    padding :0% 8%;
  }
  .left{
    max-height: 300px;
  }
 
}

@media only screen and (max-width: 768px) {
  .right{
  padding:0rem;
}
 .About{
	padding-top:3rem;
	/* padding-bottom:2rem; */
 }
 .right{
	 padding-top:2rem;
 }
}


@media only screen and (max-width: 1024px) {
	header .nav-btn {
		visibility: visible;
		opacity: 1;
	}

	header nav {
		position: fixed;
		top: -120vh;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 1.5rem;
		background-color: var(--mainColor);
		transition: 0.7s;
	}

	header .responsive_nav {
		transform: translateY(120vh);
	}

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}

	nav a {
		font-size: 1.2rem;
}

}